<template>
  <v-col lg="4" md="4" sm="4" xs="6">
    <router-link :to="'/product/' + item.id">
      <v-card class="pa-3">
        <v-img :src="item.thumbnail"></v-img>
        <h3>{{ item.name }}</h3>
        <br />
        <p v-if="item.discount > 0">
          <span class="red--text">
            <span style="font-size:12px;">Rs.</span>
            <span style="font-size:16px;">{{ discountedPrice }}</span>
          </span>
          <strike class="grey--text" style="margin-left:5px;font-size:12px;">{{
            item.price
          }}</strike>
        </p>
        <p v-else>Rs. {{ item.price }}</p>
      </v-card>
    </router-link>
  </v-col>
</template>
<script>
export default {
  props: {
    item: {
      required: true,
      type: Object
    }
  },
  computed: {
    discountedPrice() {
      return this.item.price - this.item.price * (this.item.discount / 100);
    }
  }
};
</script>
<style scoped></style>
