<template>
  <div class="homepage">
    <vue-pull-refresh :on-refresh="onRefresh">
      <Header />
      <v-container v-if="displayPlaceholder">
        <PostPlaceholder v-for="p in 3" :key="p"></PostPlaceholder>
      </v-container>
      <ProductList :items="products" />
    </vue-pull-refresh>
  </div>
</template>

<script>
import Header from "@/components/partials/Header";
import ProductList from "@/components/ProductList";
import PostPlaceholder from "@/components/placeholders/PostPlaceholder";

const fb = require("../firebaseConfig.js");

import VuePullRefresh from "vue-pull-refresh";
import { setInterval } from "timers";

export default {
  components: {
    Header,
    ProductList,
    PostPlaceholder,
    "vue-pull-refresh": VuePullRefresh
  },
  data() {
    return {
      products: [],
      errors: [],
      displayPlaceholder: true,
      fetchingData: true
    };
  },
  created() {
    this.fetchData();
  },
  methods: {
    fetchData: function() {
      fb.productsCollection
        .orderBy("createdOn", "desc")
        .onSnapshot(querySnapshot => {
          let productsArray = [];

          querySnapshot.forEach(doc => {
            let product = doc.data();
            product.id = doc.id;
            productsArray.push(product);
          });
          this.loadContent(productsArray);
        });
    },
    loadContent: function(response) {
      let v = this;
      setTimeout(function() {
        v.displayPlaceholder = false;
      }, 200);
      this.fetchingData = false;
      this.products = response;
    },
    onRefresh: function() {
      let v = this;
      return new Promise(function(resolve, reject) {
        v.fetchingData = true;
        v.displayPlaceholder = true;
        v.fetchData();

        setInterval(function() {
          if (v.fetchingData == false) {
            resolve();
          }
        }, 200);
      });
    }
  }
};
</script>
